import React, { Component } from 'react';
import Button from 'material-ui/Button';
import { Link, browserHistory } from 'react-router';
import TextField from 'material-ui/TextField';
import { withStyles, createStyleSheet } from 'material-ui/styles';
import { blue, grey } from 'material-ui/colors';
import { connect } from 'react-redux'
import { postLogin } from '../modules/login';

const styles = {
  loginContainer: {
    textAlign: 'center',
    textHeight: '200px',
    backgroundColor: '#fff',
    padding: '2em 0 2em 0',
    borderRadius: '5px',
    border: '1px solid rgba(0,0,0,0.1)',
    marginTop: '10%',
    boxShadow: '2px 2px 20px #ccc',
  },
  textField: {
    width: '40%',
    minWidth: '200px',
  },
  textColor: {
    color: grey[800],
    padding: '20px',
    fontSize: '0.9em',
    margin: '20px',
  },
  link: {
    color: blue[800],
    textDecoration: 'none',
    '&:hover': {
      color: blue[600],
    },
  },
};

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      msg: '',
    };
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.userProfile.userProfile && this.props.userProfile.userProfile.userInfo.id) {
      const url = this.props.location.pathname;
      browserHistory.push('/profile');    
    }
  }

  componentDidMount() {
    if (this.props.userProfile.userProfile && this.props.userProfile.userProfile.userInfo.id) {
      const url = this.props.location.pathname;
      browserHistory.push('/profile');    
    }
  }

  // 双箭头，高级用法（待研究）
  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  }

  onSubmit = () => {
    this.props && this.props.postLogin(this.state.email, this.state.password);
  }

  render() {
    const userInfo = this.props.userProfile.userProfile ? this.props.userProfile.userProfile.userInfo:null;
    return (
      <div className={this.props.classes.loginContainer}>
        <div>
          <TextField className={this.props.classes.textField}
            id="email"
            label="E-mail"
            margin="normal"
            autoFocus
            required
            placeholder='Enter the E-mail address'
            onChange={this.handleChange('email')}
          />
        </div>
        <div>
          <TextField className={this.props.classes.textField}
            id="password"
            label="Password"
            type="password"
            margin="normal"
            placeholder='Set password here, at least 4 characters long'            
            required
            onChange={this.handleChange('password')}
          />
        </div>
        <div className={this.props.classes.textColor}>
          Need an account?
          <Link to="/signup" className={this.props.classes.link}> Sign up here </Link>
        </div>
        <div>
          <Button
            raised
            color="primary"
            className={this.props.classes.button}
            onClick={this.onSubmit}
          >
             Login
          </Button>
        </div>
        {/* {alert(this.props.userProfile.message)} */}
      </div>
    );
  }
}


const mapDispatchToProps = {
  postLogin,
}

const mapStateToProps = (state) => ({
  userProfile : state.login,
})

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(createStyleSheet(styles))(Login));



