import axios from 'axios';
import { devServerAddress } from '../../../global/const';

// ------------------------------------
// Constants
// ------------------------------------
export const POST_LOGIN_START = 'POST_LOGIN_START';
export const POST_LOGIN_DONE = 'POST_LOGIN_DONE';

// ------------------------------------
// Actions
// ------------------------------------
export function postLoginStart() {
  return {
    type: POST_LOGIN_START,
    message: 'Start fetching user profile data...',
  };
}

export function postLoginDone(users, msg) {
  return {
    type: POST_LOGIN_DONE,
    userProfile: users,
    message: msg,
  };
}

export function postLogin(email, password) {
  return (dispatch, getState) => {
    dispatch(postLoginStart());
    axios.post(
      `${devServerAddress}/login`,
      {
        email,
        password,
      })
      .then((response) => {
        console.log('返回数据', response);
        if (response.data.status === 'success') {
          return dispatch(postLoginDone(response.data, response.data.msg));
        } else if (response.data.status === 'failure') {
          return dispatch(postLoginDone(null, response.data.msg));          
        }
      })
      .catch((error) => {
        return dispatch(postLoginDone(null, 'Network error.'));
      });
  };
}

// ------------------------------------
// Action Handlers
// ------------------------------------

const USERS_ACTION_HANDLERS = {
  [POST_LOGIN_START]: (state, action) => {
    return ({ ...state, message: action.message });
  },
  [POST_LOGIN_DONE]: (state, action) => {
    return ({ ...state, userProfile: action.userProfile, message: action.message });
  },
};

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {
  userProfile: null,
  message: '',
};

export default function getUserReducer(state = initialState, action) {
  const handler = USERS_ACTION_HANDLERS[action.type];
  return handler ? handler(state, action) : state;
}



